<template>
  <swiper ref="swiper" v-if="banners.length">
    <swiper-item  v-for="(item, index) in banners" :key="index">
      <a href="#">
      <!--<a :href="bannersAppUrl">-->
        <img :src="item.content" alt="">
      </a>
    </swiper-item>
  </swiper>
</template>

<script>
  import {Swiper, SwiperItem} from '../../../components/common/swiper/'
  //由于swiper中设置了index所以这里引用到swiper文件夹即可

  export default {
    name: "HomeSwiper",
    components: {
      Swiper,
      SwiperItem
    },
    props: {
      banners: {
        type: Array,
        default:[]
      },
      bannersAppUrl:{
        type:String,
        default:"#"
      }
      //banners 为swiper的数据
    },
    methods: {
      stopTimer() {
        this.$refs.swiper.stopTimer()
      },
      startTimer() {
        if (this.$refs.swiper) {
          this.$refs.swiper.startTimer()
        }
      }
    }
  }
</script>

